<template>
  <div>
    <h1>Hello user-{{$route.params.id}}</h1>
    <h1>Hello! C-{{user.id}}</h1>
    <h2>User name: {{user.name}}</h2>
    <h2>User age: {{user.age}}</h2>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'User',
  props: ['id'],
  data () {
    return {
      user: {}
    }
  },
  /*created () {
      axios.get('/api/get-user', {
          params: {
            id : this.$route.params.id
          }
        })
        .then((response) => {
            this.user = response.data
        })
  },*/
  beforeRouteEnter (to, from, next) {
    axios.get('/api/get-user', {
        params: {
          id : to.params.id
        }
      })
      .then((response) => {
        next(vm => {
          vm.user = response.data
        })
      })
  }
}
</script>

<style scoped>
  h1, h2 {
    font-weight: normal;
    color : red;
  }
</style>



